/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import placesData from './data-bb-places-http';

const placesDataModuleKey = 'data-bb-places-http-ng';

export const placesDataKey = 'data-bb-places-http-ng:placesData';

export default ng
  .module(placesDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(placesDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       placesData(config),
      ],
    };
  }])
  .name;



